import Vue from "vue";
import Router from "vue-router";
import Home from "@/views/Home.vue";
import Blank from "@/views/Blank.vue";

const Auth = () => import(/* webpackChunkName: "auth" */ "@/views/Auth.vue");
const DiscordAuth = () => import(/* webpackChunkName: "auth" */ "@/components/auth/DiscordAuth.vue");
const StandaloneAuth = () => import(/* webpackChunkName: "auth" */ "@/components/auth/StandaloneAuth.vue");

import General from "@/views/General.vue";
import StatCalc from "@/components/general/StatCalc.vue";
const AttackPerCal = () => import(/* webpackChunkName: "attack" */ "@/components/general/AttackPerCal.vue");
const Jobs = () => import(/* webpackChunkName: "jobs" */ "@/views/Jobs.vue");
const JobView = () => import(/* webpackChunkName: "jobs" */ "@/components/job/JobView.vue");

//const Items = () => import(/* webpackChunkName: "items" */ "@/views/Items.vue");
const Items = () => import(/* webpackChunkName: "old-items" */ "@/old/items/Items.vue");
const Item = () => import(/* webpackChunkName: "old-items" */ "@/old/items/Item.vue");
const ItemSearch = () => import(/* webpackChunkName: "old-items" */ "@/old/items/ItemSearch.vue");
const ItemHome = () => import(/* webpackChunkName: "old-items" */ "@/old/items/ItemHome.vue");
const ItemToolkit = () => import(/* webpackChunkName: "old-items" */ "@/old/items/ItemToolkit.vue");
const ItemToolkitIndex = () => import(/* webpackChunkName: "old-items" */ "@/old/items/ItemToolkitIndex.vue");
const ItemDropIndex = () => import(/* webpackChunkName: "drop-index" */ "@/old/items/ItemDropIndex.vue");
const ItemDrop = () => import(/* webpackChunkName: "old-items" */ "@/old/items/ItemDrop.vue");
const ItemDropGroup = () => import(/* webpackChunkName: "old-items" */ "@/old/items/ItemDropGroup.vue");
const ItemDropShow = () => import(/* webpackChunkName: "drop-view" */ "@/old/items/ItemDropShow.vue");
const ItemShopToolkit = () => import(/* webpackChunkName: "items" */ "@/components/item/shop/ShopToolKit.vue");

const Maps = () => import(/* webpackChunkName: "maps" */ "@/views/Maps.vue");
const MapViewMock = () => import(/* webpackChunkName: "maps" */ "@/components/map/MapViewMock.vue");

const Monsters = () => import(/* webpackChunkName: "monsters" */ "@/views/Monsters.vue");
const Monster = () => import(/* webpackChunkName: "monsters" */ "@/components/monsters/MonsterView.vue");

const Skills = () => import(/* webpackChunkName: "skills" */ "@/views/Skills.vue");
const SkillView = () => import(/* webpackChunkName: "skills" */ "@/components/skill/SkillView.vue");

// const Text = () => import(/* webpackChunkName: "text" */ "@/views/Text.vue");
const UiStrings = () => import(/* webpackChunkName: "text" */ "@/old/uistrings/UiStrings.vue");
const UiStringBrowse = () => import(/* webpackChunkName: "text" */ "@/old/uistrings/UiStringBrowse.vue");
const UiStringMidLup = () => import(/* webpackChunkName: "text" */ "@/old/uistrings/UiStringMidLup.vue");

const Resources = () => import(/* webpackChunkName: "resources" */ "@/views/Resources.vue");
const ResourcesHome = () => import(/* webpackChunkName: "resources" */ "@/old/resources/ResourcesHome.vue");
const GameClient = () => import(/* webpackChunkName: "resources" */ "@/old/resources/gameclient/GameClient.vue");

const GameInfoIndex =()=> import(/* webpackChunkName: "gameinfo" */ "@/new/GameInfo/index.vue");
const Donation =()=> import(/* webpackChunkName: "gameinfo" */ "@/new/GameInfo/Donation.vue");
const HeroStat =()=> import(/* webpackChunkName: "gameinfo" */ "@/new/GameInfo/HeroStat.vue");
const Ladder =()=> import(/* webpackChunkName: "gameinfo" */ "@/new/Event/Ladder.vue");
const Appellation =()=> import(/* webpackChunkName: "appellation" */ "@/new/Appellation/index.vue");
const Title =()=> import(/* webpackChunkName: "gameinfo" */ "@/new/Appellation/Title.vue");
const AppellationBook =()=> import(/* webpackChunkName: "gameinfo" */ "@/new/Appellation/collection.vue");
const PromiseInfo =()=> import(/* webpackChunkName: "gameinfo" */ "@/new/Cash/Promise.vue");
const Labyrinth =()=> import(/* webpackChunkName: "gameinfo" */ "@/new/GameInfo/Labyrinth.vue");
const Tax =()=> import(/* webpackChunkName: "gameinfo" */ "@/new/GameInfo/Taxes.vue");
const Feat =()=> import(/* webpackChunkName: "appellation" */ "@/new/GameInfo/feat.vue");
const Event =()=> import(/* webpackChunkName: "appellation" */ "@/new/Event/index.vue");
const GuildIndex =()=> import(/* webpackChunkName: "guild" */ "@/new/Guild/index.vue");
const GuildInfo =()=> import(/* webpackChunkName: "guild" */ "@/new/Guild/info.vue");
const GuildContent =()=> import(/* webpackChunkName: "guild" */ "@/new/Guild/Content.vue");
const GuildMission =()=> import(/* webpackChunkName: "guild" */ "@/new/Guild/Mission.vue");
const FarmIndex = () => import(/* webpackChunkName: "farm" */ "@/new/Farm/index.vue");
const FarmSkill = () => import(/* webpackChunkName: "farm" */ "@/new/Farm/Skill.vue");
const FarmCultivate = () => import(/* webpackChunkName: "farm" */ "@/new/Farm/Cultivate.vue");
const FarmCook = () => import(/* webpackChunkName: "farm" */ "@/new/Farm/Cook.vue");
const Bingo = () => import(/* webpackChunkName: "Event" */ "@/new/Event/Bingo.vue");
const FarmFish = () => import(/* webpackChunkName: "farm" */ "@/new/Farm/Fish.vue");
const MainQuest = () => import(/* webpackChunkName: "gameinfo" */ "@/new/GameInfo/MainQuest.vue");
const Repute = () => import(/* webpackChunkName: "gameinfo" */ "@/new/GameInfo/Repute.vue");
const Fishing = () => import(/* webpackChunkName: "event" */ "@/new/Event/Fishing.vue");
const Hidshop = () => import(/* webpackChunkName: "cash" */ "@/new/Cash/Hidshop.vue");
const CashIndex = () => import(/* webpackChunkName: "cash" */ "@/new/Cash/index.vue");
const Achievement = () => import(/* webpackChunkName: "Appellation" */ "@/new/Appellation/Achieve.vue");
const EquipmentBook = () => import(/* webpackChunkName: "GameInfo" */ "@/new/GameInfo/EquipmentBook.vue");
const VIP = () => import(/* webpackChunkName: "cash" */ "@/new/Cash/VIP.vue");
const Alteia = () => import(/* webpackChunkName: "cash" */ "@/new/Event/Alteia.vue");
const BoardGame = () => import(/* webpackChunkName: "cash" */ "@/new/Event/BoardGame.vue");
const CashShop = () => import(/* webpackChunkName: "cash" */ "@/new/Cash/CashShop.vue");
const Growup = () => import(/* webpackChunkName: "gameinfo" */ "@/new/GameInfo/Growup.vue");
import NotFound from "@/views/NotFound.vue";

Vue.use(Router)

export default new Router({
    mode: "history",
    base: process.env.BASE_URL,
    routes: [
        {
            path: "/",
            name: "home",
            component: Home,
        },
        {
            path: "/auth",
            component: Auth,
            children: [
                {
                    path: "discord",
                    component: DiscordAuth,
                    meta: {
                        redemption: true,
                    },
                },
                {
                    path: "redirect",
                    component: StandaloneAuth,
                    name: "auth",
                    meta: {
                        redemption: true,
                    },
                }
            ],
        },
        {
            path: "/general",
            component: Blank,
            children: [
                {
                    path: "",
                    component: General,
                },
                {
                    // path: "stat/:statName?",
                    path: "stat",
                    component: StatCalc,
                },
                {
                    path: "attack",
                    component: AttackPerCal,
                },
                {
                  path: 'tax',
                  name: 'info-tax',
                  component: Tax
                },
                {
                  path: 'herostat',
                  name: 'info-herostat',
                  component: HeroStat
                },
            ],
        },
        {
            path: "/jobs",
            component: Blank,
            meta: {
                auth: !process.env.VUE_APP_ALLOW_UNAUTH,
            },
            children: [
                {
                    path: "",
                    name: "jobs",
                    component: Jobs,
                },
                {
                    path: ":jobId",
                    name: "job-view",
                    component: JobView,
                    props: true,
                }
            ],
        },
        {
            path: "/items",
            component: Items,
            meta: {
                auth: !process.env.VUE_APP_ALLOW_UNAUTH,
            },
            children: [
              {
                path: '',
                name: 'item-home',
                component: ItemHome
              },
              {
                path: 'search',
                name: 'item-search',
                component: ItemSearch
              },
              {
                path: 'drops',
                component: Blank,
                children: [
                {
                    path: '',
                    name: 'item-drop-index',
                    component: ItemDropIndex
                },
                {
                    path: 'Dungeon',
                    name: 'item-drop-dungeon',
                    component: ItemDropShow
                },
                {
                    path: 'requestBorard',
                    name: 'item-drop-requestBorard',
                    component: ItemDropShow
                },
                {
                    path: 'weekDungeon',
                    name: 'item-drop-weekDungeon',
                    component: ItemDropShow
                },
                {
                    path: 'specialDungeon',
                    name: 'item-drop-specialDungeon',
                    component: ItemDropShow
                },
                {
                    path: 'balanceDungeon',
                    name: 'item-drop-balanceDungeon',
                    component: ItemDropShow
                },
                {
                    path: 'eventDungeon',
                    name: 'item-drop-eventDungeon',
                    component: ItemDropShow
                },
                {
                    path: 'dragonDungeon',
                    name: 'item-drop-dragon',
                    component: ItemDropShow
                },                      
                ]
              },
              {
                path: 'shop/:id?',
                name: 'item-shop',
                component: ItemShopToolkit,
              },
              {
                path: ':itemId',
                name: 'item-view',
                component: Item
              },
              // {
              //   path: 'toolkit',
              //   component: ItemToolkit,
              //   children: [
              //     {
              //       path: 'index',
              //       name: 'item-toolkit',
              //       component: ItemToolkitIndex
              //     },
              //     {
              //       path: 'dropgroup/:id?',
              //       name: 'item-drop-group',
              //       component: ItemDropGroup
              //     },
              //     {
              //       path: 'drop/:id?',
              //       name: 'item-drop',
              //       component: ItemDrop
              //     },
              //     {
              //       path: 'itemshop/:id?',
              //       name: 'item-shop',
              //       component: ItemShopToolkit,
              //     }
              //   ]
              // }
            ]
        },
        {
            path: "/maps",
            component: Blank,
            children: [
                {
                    path: "",
                    name: "maps",
                    component: Maps,
                },
                {
                    path: ":id",
                    name: "map-view",
                    props: true,
                    component: MapViewMock,
                }
            ],
        },
        {
            path: "/monsters",
            component: Blank,
            children: [
                {
                    path: "",
                    name: "monsters",
                    component: Monsters,
                },
                {
                    path: ":monsterSlug",
                    name: 'monster-view',
                    props: true,
                    component: Monster,
                }
            ],
        },
        {
            path: "/skills",
            component: Blank,
            meta: {
                auth: !process.env.VUE_APP_ALLOW_UNAUTH,
            },
            children: [
                {
                    path: "",
                    name: "skills",
                    component: Skills,
                },
                {
                    path: ":skillSlug",
                    name: "skill-view",
                    props: true,
                    component: SkillView,
                },
            ],
        },
        {
            path: "/text",
            component: Blank,
            meta: {
                auth: true,
            },
            children: [
                {
                    path: '',
                    redirect: 'uistring',
                },
                {
                    path: "uistring",
                    component: UiStrings,
                    children: [
                      {
                        path: '',
                        redirect: 'browse'
                      },
                      {
                        path: 'browse',
                        component: UiStringBrowse,
                        name: 'uistring-browse',
                      },
                      {
                        path: 'midlup',
                        name: 'uistring-midlup',
                        component: UiStringMidLup
                      },
                      {
                        path: '*',
                        component: NotFound
                      }
                    ]
                },
            ],
        },
        {
            path: "/resources",
            component: Blank,
            children: [
                {
                    path: "",
                    name: "resources",
                    component: ResourcesHome,
                },
                {
                  path: 'downloadgame',
                  name: 'resources-game-client',
                  component: GameClient
                }
            ],
        },
        {
            path: "/appellation",
            component: Blank,
            children: [
              {
                path: '',
                name: 'appellation',
                component: Appellation
              },
              {
                path: 'title',
                name: 'appellation-title',
                component: Title
              },
              {
                path: 'book',
                name: 'appellation-book',
                component: AppellationBook
              },
              {
                path: 'achievement',
                name: 'appellation-achievement',
                component: Achievement
              },
            ]
        },
        {
          path: "/event",
          component: Blank,
          children: [
            {
              path: '',
              name: 'event',
              component: Event
            },
            {
              path: 'ladder',
              name: 'event-ladder',
              component: Ladder
            },
            {
              path: 'bingo',
              name: 'event-bingo',
              component: Bingo
            },
            {
              path: 'fishing',
              name: 'event-fishing',
              component: Fishing
            },
            {
              path: 'alteia',
              name: 'event-alteia',
              component: Alteia
            },
            {
              path: 'boardgame',
              name: 'event-boardgame',
              component: BoardGame
            },
          ]
        },
        {
          path: "/guild",
          component: Blank,
          children: [
            {
              path: '',
              name: 'guild',
              component: GuildIndex
            },
            {
              path: 'info',
              name: 'guild-info',
              component: GuildInfo
            },
            {
              path: 'content',
              name: 'guild-content',
              component: GuildContent
            },
            {
              path: 'mission',
              name: 'guild-mission',
              component: GuildMission
            },
          ]
        },
        {
          path: "/farm",
          component: Blank,
          children: [
            {
              path: '',
              name: 'farm',
              component: FarmIndex
            },
            {
              path: 'skill',
              name: 'farm-skill',
              component: FarmSkill
            },
            {
              path: 'cultivate',
              name: 'farm-cultivate',
              component: FarmCultivate
            },
            {
              path: 'cook',
              name: 'farm-cook',
              component: FarmCook
            },
            {
              path: 'fish',
              name: 'farm-fish',
              component: FarmFish
            },
          ]
        },
        {
          path: "/cash",
          component: Blank,
          children: [
            {
              path: '',
              name: 'cash',
              component: CashIndex
            },
            {
              path: 'promise',
              name: 'cash-promise',
              component: PromiseInfo
            },
            {
              path: 'vip',
              name: 'cash-vip',
              component: VIP
            },
            {
              path: 'hidshop',
              name: 'cash-hidshop',
              component: Hidshop
            },
            {
              path: 'cashshop',
              name: 'cash-shop',
              component: CashShop
            },
          ]
        },
        {
            path: "/gameInfo",
            component: Blank,
            children: [
              {
                path: '',
                name: 'game-info',
                component: GameInfoIndex
              },
              {
                path: 'donation',
                name: 'info-donation',
                component: Donation
              },
              {
                path: 'labyrinth',
                name: 'info-labyrinth',
                component: Labyrinth
              },
              {
                path: 'feat',
                name: 'feat',
                component: Feat
              },
              {
                path: 'mainquest',
                name: 'mainquest',
                component: MainQuest
              },
              {
                path: 'repute',
                name: 'info-repute',
                component: Repute
              },
              {
                path: 'equipment',
                name: 'info-equipment',
                component: EquipmentBook
              },
              {
                path: 'growup',
                name: 'info-growup',
                component: Growup
              },
            ]
        },
        {
            path: "*",
            component: NotFound,
        },
    ]
})
