



















































import Vue from 'vue';

import StatCalcProvider,{ IStatCalcResult } from "@/api/StatCalcProvider";
import ITypedMap from '@/models/util/ITypedMap';
import { getFeatureState, netFeatures } from '@/api/FeaturesProvider';

interface ILimits {
    minPercent: number;
    maxPercent: number;
    minStat: number;
    maxStat: number;
}

interface IFloorLimits{
    maxStat:number;
    maxPercent:number;
    decreaseStat:number;
}

interface IData {
    level: number;
    value: number;
    percent: number;
    limits: ILimits;
    inputType: string;
    floor: number;
    floorLimits:IFloorLimits
}

function getLevelCap(): number {
    if (getFeatureState(netFeatures.Use40Cap)) {
        return 40;
    }
    
    if (getFeatureState(netFeatures.Use70Cap)) {
        return 70;
    }
    
    return 95;
}

export default Vue.extend({
    props: {
        "stat": {
            type: String as () => string,
        }
    },
    data(): IData {
        return {
            level: getLevelCap(),
            value: 0,
            percent: 0,
            limits: {
                minPercent: 0,
                maxPercent: 100,
                minStat: 0,
                maxStat: 99999
            },
            inputType: "STAT",
            floor:0,
            floorLimits: {
                maxStat: 0,
                maxPercent: 0,
                decreaseStat:0
            },
        };
    },
    watch: {
        level() {
            this.loadStatCap().then(() => {
                this.calculate();
            });
        },
        percent() {
            if (this.inputType == "PERCENT") {
                this.calculate();
            }
        },
        value() {
            if (this.inputType == "STAT") {
                this.calculate();
            }
        },
        floor(){
            if(this.floor >= 14 && this.floor <= 25){
                this.floorStat();
            }else{
                this.floorLimits.decreaseStat = 0;
                this.floorLimits.maxStat = 0;
                this.floorLimits.maxPercent = 0;
                if (this.percent > this.limits.maxPercent) {
                    this.percent = this.limits.maxPercent;
                    return false;
                }
                if (this.percent < this.limits.minPercent) {
                    this.percent = this.limits.minPercent;
                    return false;
                }
            }
            this.calculate();
        }
    },
    computed: {
        isCritDmg(): boolean {
            return this.stat === "criticalDamage";
        },
        showMaxStat(): number {
            if(this.floorLimits.maxStat != 0){
                return this.floorLimits.maxStat;
            }else{
                return this.limits.maxStat
            }
        },
        showMaxPercent(): number {
            if(this.floorLimits.maxPercent != 0){
                return this.floorLimits.maxPercent;
            }else{
                return this.limits.maxPercent
            }
        }
    },
    created() {
        this.loadStatCap();
    },
    methods: {
        async floorStat(){
            const multiple = this.floor - 13; //确定倍数
            let baseDecrease = 0;
            switch (this.stat) {
                case 'finalDamage':
                    baseDecrease = 1200;
                    break;
                case 'critical':
                    baseDecrease = 100000;
                    break;
                case 'criticalDamage':
                    baseDecrease = 450000;
                    break;
                case 'defense':
                    baseDecrease = 30000;
                    break;
                default:
                    break;
            }
            this.floorLimits.decreaseStat = baseDecrease * multiple;
            this.floorLimits.maxStat = this.limits.maxStat + baseDecrease * multiple;
            if(this.isCritDmg){
                this.floorLimits.maxPercent = Math.round(this.floorLimits.maxStat/1348924*100 + 200);
            }else{
                this.floorLimits.maxPercent = Math.round(this.floorLimits.maxStat * this.limits.maxPercent / this.limits.maxStat);
            }

        },
        loadStatCap() {
            if (isNaN(this.level) || this.level <= 0 || this.level > 100) {
                this.limits.maxStat = 0;
                return Promise.resolve();
            }

            return StatCalcProvider.getStatCap(this.level, this.stat).then((caps) => {
                // Check again in case we beat the network
                if (isNaN(this.level) || this.level <= 0 || this.level > 100) {
                    this.limits.maxStat = 0;
                    return;
                }

                this.limits.maxPercent = caps.percent * 100;
                this.limits.maxStat = Math.round(caps.value);
                this.limits.minPercent = 0;

                if (this.isCritDmg) {
                    this.limits.maxPercent += 200;
                    this.limits.minPercent += 2;
                }

                this.percent = this.getTruncatedPercent() * 100;
            });
        },
        unfocus(evt: Event) {
            //@ts-ignore
            evt.target.blur();
        },
        select(evt: Event) {
            if (evt.target == null || !(evt.target instanceof Element) || evt.target.parentNode == null) {
                return;
            }

            this.inputType = (<ITypedMap<any>>(<HTMLElement>evt.target.parentNode).attributes)["value"].value;
            (this.$refs[this.inputType] as HTMLFormElement).focus();
        },
        capValue(evt: Event) {
            this.value = Number(this.value);
            if (this.value > 9999999) {
                this.value = 9999999;
                return false;
            } 
            if (this.value < this.limits.minStat) {
                this.value = this.limits.minStat;
                return false;
            }
        },
        capPercent(evt: Event) {
            this.percent = Number(this.percent);
            if(this.floorLimits.maxPercent != 0){
                console.log(444)
                if(this.percent > this.floorLimits.maxPercent){
                    this.percent = this.floorLimits.maxPercent;
                    return false;
                }
                if (this.percent < this.limits.minPercent) {
                    this.percent = this.limits.minPercent;
                    return false;
                }

            }else{
                if (this.percent > this.limits.maxPercent) {
                    this.percent = this.limits.maxPercent;
                    return false;
                }
                if (this.percent < this.limits.minPercent) {
                    this.percent = this.limits.minPercent;
                    return false;
                }
            }
        },
        getTruncatedValue() {
            if(this.floorLimits.maxStat!=0){
                return Math.min(this.floorLimits.maxStat, Math.max(this.limits.minStat, this.value));
            }else{
                return Math.min(this.limits.maxStat, Math.max(this.limits.minStat, this.value));
            }
            
        },
        getTruncatedPercent() {
            if(this.floorLimits.maxPercent!=0){
                return Math.min(this.floorLimits.maxPercent, Math.max(this.limits.minPercent, this.percent / 100));
            }else{
                return Math.min(this.limits.maxPercent, Math.max(this.limits.minPercent, this.percent / 100));
            }
            
        },
        async calculate() {
            if (this.inputType == "STAT") {
                let percentVal = await StatCalcProvider.calculatePercent(this.level, this.stat, this.getTruncatedValue(),this.floorLimits.maxStat);
                let percent = Math.floor(percentVal.percent * 100);
                if (this.isCritDmg) {
                    percent += 200;
                }

                this.percent = percent;
            } else {
                let percent = this.getTruncatedPercent();
                // if (this.isCritDmg) {
                //     percent -= 2;
                // }

                let statVal = await StatCalcProvider.calculateValue(this.level, this.stat, percent,this.floorLimits);
                this.value = Math.floor(statVal.value);
            }
        },
    },
});
